    position: relative;
    right: 30px;
  }

  .newsletter {
    width: 100%;
    border-top: 1px solid #0070A0;
    padding-top: 12px;
    font-size: 14px;
  }

  .new_form {
    border-top: 1px solid #0070A0;
    border-bottom: 1px solid #0070A0;
  }

  .data {
    padding: 0 35px;
  }
}